export default {
	data() {
		return {
			sessionId:'',
			myData:[],
		}
	},
	onLoad(option) {
		this.sessionId = option.sessionId
		console.log(this.sessionId)
		this.init()
	},
	methods: {
		init(){
			uni.request({
				url:'https://www.nssnail.com/wx/personal/getDonatorRecord',
				data:{
					sessionId:this.sessionId
				},
				success: (res) => {
					if(res.data.stat == 1){
						this.myData = res.data.data
						this.myData.forEach((item)=>{
							if(item.stat == 1){
								item.stat = '募捐中'
							}else if(item.stat == 2){
								item.stat = '已取消'
							}
						})
					}else if(res.data.stat == 3 || res.data.stat == 4){
						uni.showModal({
						    title: '提示',
						    content: '登录过期，请重新登录',
						    success: function (res) {
						        if (res.confirm) {
						            uni.navigateTo({
						            	url:'../myInit/myInit'
						            })
						        }
						    }
						});
					}
				}
			})
		}
	}
}
